
import { createStore } from 'redux';


/*
 * 
 *channelStore  state
 * 1: ;
 * 2: 
 * 3: 
 * 4: 
 */

const reducerChannel = ( state, action ) => {

    var type = action.type;
    switch (type) {
        case 'channel':
            return 'c' + action.num;
        default:
            return 'c';
    }

}

const channelStore = createStore(reducerChannel, 'c');


export default channelStore;


