
import { createStore } from 'redux';


/*
 * 
 *channelStore  state
 * 1: ;
 * 2: 
 * 3: 
 * 4: ;
 * 5: ;
 * 6: ip;
 * 7: 
 * 8: ;
 */

const masterReducer = ( state, action ) => {

    var type = action.type;
    switch (type) {
        case 'master':
            return 'm' + action.num;
        default:
            return 'm';
    }

}

const masterStore = createStore(masterReducer, 'm');


export default masterStore;


