
import React from 'react';
import axios from 'axios';
import { Form, Input, Tooltip, Icon, Cascader, Select, Row, Col, Checkbox, Button } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;


class AddUserForm extends React.Component {
    constructor(props) {
        super(props);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                values.isNew = 'true';
                axios
                    .post(this.props.url, values)
                    .then((json) => {
                        json = json.data;
                        if ( json.success ) {
                            alert('');
                            location.reload();
                        } else {
                            if ( json.code && json.code == 2000 ) {
                                alert(',')
                            } else {
                                alert('')
                            }
                        }
                    })
                    .catch(()=>{
                        alert('');
                    })
            }
        });
    }
    checkNum = (rule, value, callback) => {
        if (value && value >= 1) {
            callback('1');
        } else {
            callback();
        }
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 6 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 14 },
            },
        };
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0,
                },
                sm: {
                    span: 14,
                    offset: 6,
                },
            },
        };
        const prefixSelector = getFieldDecorator('prefix', {
            initialValue: '86',
        })(
            <Select className="icp-selector">
                <Option value="86">+86</Option>
            </Select>
        );
        return (
            <Form>
                <FormItem
                    {...formItemLayout}
                    label=""
                    hasFeedback
                    >
                    {getFieldDecorator('username', {
                        rules: [{
                            required: true, message: '',
                        }],
                    })(
                        <Input />
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label=""
                    hasFeedback
                    >
                    {getFieldDecorator('password', {
                        rules: [{
                            required: true, message: '',
                        }],
                    })(
                        <Input type="password" />
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label={(
                        <span>
                          &nbsp;
                          <Tooltip title="">
                            <Icon type="question-circle-o" />
                          </Tooltip>
                        </span>
                      )}
                    hasFeedback
                    >
                    {getFieldDecorator('searchname', {
                        rules: [{ required: true, message: ''}],
                    })(
                        <Input />
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label=""
                    hasFeedback
                    >
                    {getFieldDecorator('rank', {
                        rules: [
                            { required: true, message: '' },
                        ],
                    })(
                        <Select placeholder="">
                            <Option value="2"></Option>
                            <Option value="1"></Option>
                        </Select>
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label={(
                        <span>
                          &nbsp;
                          <Tooltip title="1">
                            <Icon type="question-circle-o" />
                          </Tooltip>
                        </span>
                      )}
                    hasFeedback
                    >
                    {getFieldDecorator('modify_increase', {
                        rules: [{
                            required: true, message: '',
                        }, {
                            validator: this.checkNum,
                        }],
                    })(
                        <Input />
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label={(
                        <span>
                          &nbsp;
                          <Tooltip title="1">
                            <Icon type="question-circle-o" />
                          </Tooltip>
                        </span>
                      )}
                    hasFeedback
                    >
                    {getFieldDecorator('modify_expense', {
                        rules: [{
                            required: true, message: '',
                        }, {
                            validator: this.checkNum,
                        }],
                    })(
                        <Input />
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label=""
                    hasFeedback
                    >
                    {getFieldDecorator('crawl_url', {
                        rules: [
                            { required: true, message: '' },
                        ],
                    })(
                        <Select placeholder="">
                            <Option value="1">geek-maker</Option>
                            <Option value="2">batpk</Option>
                        </Select>
                    )}
                </FormItem>
                <FormItem {...tailFormItemLayout}>
                    <Button type="primary" htmlType="button" onClick={this.handleSubmit} size="large"></Button>
                </FormItem>
            </Form>
        );
    }
}

const AddUser = Form.create()(AddUserForm);

export default AddUser;


